        padding:0!important;
    }
    .pageWrap ul{
        float:none;
        margin:0 auto;
    }
    .blockRecords{
        display:block;
        margin:29px 0 0 47px!important;
    }
    .table-responsive{
        border:none!important;
    }
    .error-image img{
        width:80%;
    }
    .navbar-main .navbar-items a{
        display: block;
        font-size:16px;
        text-align:center;
        width:100%;
        line-height:60px;
