    .h5Menu,.h5{
        display:block;
    }
    .pcMenu,.pc{
        display:none;
    }
    .banner-index{
        padding:30px 0;
    }
    .search-error .search-error-wrap{
        width:100%;
    }
    .search-error-wrap #search-bar-error{
        width:65%;
    }
